.header {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  background-color: black; }
  .header__title {
    font-size: 25px;
    color: ivory; }
    .header__title--red {
      font-weight: bold;
      -webkit-animation: change-background 2000ms linear infinite;
              animation: change-background 2000ms linear infinite; }

@-webkit-keyframes change-background {
  0% {
    color: red; }
  50% {
    color: darkred; }
  100% {
    color: red; } }

@keyframes change-background {
  0% {
    color: red; }
  50% {
    color: darkred; }
  100% {
    color: red; } }

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
  color: ivory;
  font-size: 20px;
  height: 100%;
  width: 100%; }
  .footer span {
    font-style: italic; }
  .footer i {
    -webkit-animation: change-background 2000ms linear infinite;
            animation: change-background 2000ms linear infinite;
    color: red; }

.item {
  grid-area: "cell";
  background: red;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 120px;
  cursor: pointer;
  font-weight: bold;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: ivory; }
  .item:hover, .item:active {
    background: darkred;
    border-radius: 5px;
    -webkit-transition: all 200ms ease;
    transition: all 200ms ease; }
  .item--winning {
    background: black;
    -webkit-transition: all 200ms ease;
    transition: all 200ms ease; }
    .item--winning:hover, .item--winning:active {
      background: black; }
  @media all and (max-width: 480px) {
    .item {
      font-size: 80px; } }

.board {
  display: grid;
  grid-template-areas: "cell cell cell" "cell cell cell" "cell cell cell";
  grid-template-rows: 180px 180px 180px;
  grid-template-columns: 180px 180px 180px;
  grid-gap: 10px;
  background-color: black; }
  @media all and (max-width: 480px) {
    .board {
      grid-template-rows: 100px 100px 100px;
      grid-template-columns: 100px 100px 100px;
      grid-gap: 5px; } }

input[type="range"] {
  height: 25px;
  -webkit-appearance: none;
  width: 100%;
  margin: 0px; }

input[type="range"]:focus {
  outline: none; }

input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 7px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 0px 0px 0px #000000;
  background: #000000;
  border-radius: 3px;
  border: 0px solid #010101; }

input[type="range"]::-webkit-slider-thumb {
  box-shadow: 0px 0px 0px #000031;
  border: 1px solid #00001e;
  height: 18px;
  width: 18px;
  border-radius: 2px;
  background: #ff0000;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -6px; }

input[type="range"]:focus::-webkit-slider-runnable-track {
  background: #000000; }

input[type="range"]::-moz-range-track {
  width: 100%;
  height: 7px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 0px 0px 0px #000000;
  background: #000000;
  border-radius: 3px;
  border: 0px solid #010101; }

input[type="range"]::-moz-range-thumb {
  box-shadow: 0px 0px 0px #000031;
  border: 1px solid #00001e;
  height: 18px;
  width: 18px;
  border-radius: 2px;
  background: #ff0000;
  cursor: pointer; }

input[type="range"]::-ms-track {
  width: 100%;
  height: 7px;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  color: transparent; }

input[type="range"]::-ms-fill-lower {
  background: #000000;
  border: 0px solid #010101;
  border-radius: 6px;
  box-shadow: 0px 0px 0px #000000; }

input[type="range"]::-ms-fill-upper {
  background: #000000;
  border: 0px solid #010101;
  border-radius: 6px;
  box-shadow: 0px 0px 0px #000000; }

input[type="range"]::-ms-thumb {
  margin-top: 1px;
  box-shadow: 0px 0px 0px #000031;
  border: 1px solid #00001e;
  height: 18px;
  width: 18px;
  border-radius: 2px;
  background: #ff0000;
  cursor: pointer; }

input[type="range"]:focus::-ms-fill-lower {
  background: #000000; }

input[type="range"]:focus::-ms-fill-upper {
  background: #000000; }

.timeline {
  margin: 10px 0; }

.button {
  border-radius: 4px;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  font-size: 15px; }

.controls {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0; }
  .controls button:first-child {
    margin-right: 20px; }
  .controls .controls__button {
    border-radius: 4px;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    font-size: 15px; }

.app {
  display: flex;
  flex-direction: column;
  height: 100vh; }
  .app__header {
    height: 60px; }
    @media all and (max-width: 480px) {
      .app__header {
        height: 50px; } }
  .app__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    min-height: 800px; }
    @media all and (max-width: 480px) {
      .app__content {
        min-height: 500px; } }
    .app__content__game {
      width: auto; }
      .app__content__game__result {
        text-align: center;
        margin: 10px 0; }
        .app__content__game__result p {
          font-size: 30px;
          font-weight: bold; }
          @media all and (max-width: 480px) {
            .app__content__game__result p {
              font-size: 25px; } }
  .app__footer {
    height: 40px; }

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	font-family: 'Montserrat', sans-serif;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
